// Libraries
import React, { useEffect, useState } from 'react';
import { useHistory, withRouter } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
// Style
import './createBlog.scss';
// Components
import { Input, Button, Upload, Textarea, Gap, Link } from '../../components';
// Actions
import { postToAPI, setForm, setImgPreview, updateToAPI } from '../../config/redux/action';
import axios from 'axios';


const CreateBlog = (props) => {
  const { form, imgPreview } = useSelector(state => state.createBlogReducer);
  const { title, body } = form;
  const [isUpdate, setIsUpdate] = useState(false);
  const dispatch = useDispatch()
  const history = useHistory();

  useEffect(() => {
    console.log('props: ', props);
    const id = props.match.params.id;
    if (id) {
      setIsUpdate(true);
      axios.get(`http://localhost:4000/v1/blog/post/${id}`)
        .then(res => {
          const data = res.data.data;
          console.log('response: ', data);
          dispatch(setForm('title', data.body));
          dispatch(setForm('body', data.body));
          dispatch(setImgPreview(`http://localhost:4000/${data.image}`));
        })
        .catch(err => {
          console.log('error: ', err);
        })
    }
  }, [dispatch, props])

  const onSubmit = () => {
    const id = props.match.params.id;
    if (isUpdate) {
      console.log('update data');
      updateToAPI(form, id);
    } else {
      console.log('create data');
      postToAPI(form);
    }
  }

  const onImageUpload = (e) => {
    const file = e.target.files[0];
    dispatch(setForm('image', file));
    dispatch(setImgPreview(URL.createObjectURL(file)));

  }

  return (
    <div className="blog-post">
      <Link title="Kembali" onClick={() => history.push('/')} />
      <p className="title">{isUpdate ? 'Upate' : 'Create New'} Blog Post</p>
      <Input label="Post Title" value={title} onChange={(e) => dispatch(setForm('title', e.target.value))} />
      <Upload onChange={(e) => onImageUpload(e)} image={imgPreview} />
      <Textarea value={body} onChange={(e) => dispatch(setForm('body', e.target.value))} />
      <Gap height={20} />
      <div className="button-action">
        <Button title={isUpdate ? 'Update' : 'Simpan'} onClick={onSubmit} />
      </div>
    </div>
  )
}

export default withRouter(CreateBlog);